<template>
  <div class="ham" ref="hamElement">
    <span class="bar1"></span>
    <span class="bar2"></span>
    <span class="bar3"></span>
  </div>
</template>

<script>
export default {
  name: "test",
  mounted() {
    this.$nextTick(() => {
      const hamElement = this.$refs.hamElement;

      hamElement.addEventListener("click", function () {
        this.classList.toggle("active");
      });
    });
  },
};
</script>

<style lang="scss" scoped>
/* Your existing styles */
.ham {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60px;
  height: 55px;
  cursor: pointer;
  /*  background: rgba(255,255,255,0.3); */
}
.ham span {
  display: block;
  background: #000000;
  width: 42px;
  height: 4px;
  border-radius: 3px;
  transition: margin 0.25s 0.25s, transform 0.25s;
}
.ham.active span {
  transition: margin 0.25s, transform 0.25s 0.25s;
}

.ham .bar1 {
  margin-bottom: 8px;
}
.ham .bar2 {
}
.ham .bar3 {
  margin-top: 8px;
}

.ham.active span {
  transition: margin 0.25s, transform 0.25s 0.25s;
}

.ham.active .bar1 {
  margin-top: 8px;
  margin-bottom: -4px;
  transform: rotate(45deg);
}
.ham.active .bar2 {
  transform: rotate(45deg);
}
.ham.active .bar3 {
  margin-top: -4px;
  transform: rotate(135deg);
}
</style>
